/**
 * 
 * 
 */

export function initNavbar() {
  const navbar = document.querySelector('[data-navbar]');
  if (!navbar) return;

  // 
  initScrollEffect(navbar);

  // 
  initMobileMenu(navbar);

  // 
  initSearchBox(navbar);

  // 
  initDropdowns(navbar);
}

/**
 * 
 */
function initScrollEffect(navbar) {
  const scrollThreshold = 50;
  let lastScrollY = 0;
  let ticking = false;

  const updateNavbar = () => {
    const scrollY = window.scrollY;

    // /
    if (scrollY > scrollThreshold) {
      navbar.classList.add('navbar-scrolled');
    } else {
      navbar.classList.remove('navbar-scrolled');
    }

    // /
    if (scrollY > lastScrollY && scrollY > 200) {
      navbar.classList.add('navbar-hidden');
    } else {
      navbar.classList.remove('navbar-hidden');
    }

    lastScrollY = scrollY;
    ticking = false;
  };

  window.addEventListener('scroll', () => {
    if (!ticking) {
      requestAnimationFrame(updateNavbar);
      ticking = true;
    }
  }, { passive: true });
}

/**
 * 
 */
function initMobileMenu(navbar) {
  const menuButton = navbar.querySelector('[data-menu-toggle]');
  const mobileMenu = navbar.querySelector('[data-mobile-menu]');

  if (!menuButton || !mobileMenu) return;

  menuButton.addEventListener('click', () => {
    const isOpen = mobileMenu.classList.contains('open');

    if (isOpen) {
      mobileMenu.classList.remove('open');
      menuButton.setAttribute('aria-expanded', 'false');
      document.body.classList.remove('overflow-hidden');
    } else {
      mobileMenu.classList.add('open');
      menuButton.setAttribute('aria-expanded', 'true');
      document.body.classList.add('overflow-hidden');
    }
  });

  // 
  mobileMenu.querySelectorAll('a').forEach(link => {
    link.addEventListener('click', () => {
      mobileMenu.classList.remove('open');
      menuButton.setAttribute('aria-expanded', 'false');
      document.body.classList.remove('overflow-hidden');
    });
  });

  // ESC 
  document.addEventListener('keydown', (e) => {
    if (e.key === 'Escape' && mobileMenu.classList.contains('open')) {
      mobileMenu.classList.remove('open');
      menuButton.setAttribute('aria-expanded', 'false');
      document.body.classList.remove('overflow-hidden');
    }
  });
}

/**
 * 
 */
function initSearchBox(navbar) {
  const searchToggle = navbar.querySelector('[data-search-toggle]');
  const searchBox = navbar.querySelector('[data-search-box]');
  const searchInput = navbar.querySelector('[data-search-input]');
  const searchClose = navbar.querySelector('[data-search-close]');

  if (!searchToggle || !searchBox) return;

  // 
  searchToggle.addEventListener('click', () => {
    searchBox.classList.add('open');
    searchInput?.focus();
  });

  // 
  searchClose?.addEventListener('click', () => {
    searchBox.classList.remove('open');
  });

  // ESC 
  searchInput?.addEventListener('keydown', (e) => {
    if (e.key === 'Escape') {
      searchBox.classList.remove('open');
    }
  });

  // 
  document.addEventListener('click', (e) => {
    if (!searchBox.contains(e.target) && !searchToggle.contains(e.target)) {
      searchBox.classList.remove('open');
    }
  });
}

/**
 * 
 */
function initDropdowns(navbar) {
  const dropdowns = navbar.querySelectorAll('[data-dropdown]');

  dropdowns.forEach(dropdown => {
    const trigger = dropdown.querySelector('[data-dropdown-trigger]');
    const menu = dropdown.querySelector('[data-dropdown-menu]');

    if (!trigger || !menu) return;

    // 
    if (window.matchMedia('(min-width: 768px)').matches) {
      dropdown.addEventListener('mouseenter', () => {
        menu.classList.add('open');
      });

      dropdown.addEventListener('mouseleave', () => {
        menu.classList.remove('open');
      });
    }

    // 
    trigger.addEventListener('click', (e) => {
      e.preventDefault();
      menu.classList.toggle('open');
    });
  });
}

export default { initNavbar };
