/**
 * 
 * 
 */

export function initLightbox() {
  // 
  createLightboxContainer();

  // 
  const triggers = document.querySelectorAll('[data-lightbox]');
  triggers.forEach(trigger => {
    trigger.addEventListener('click', (e) => {
      e.preventDefault();
      openLightbox(trigger);
    });
  });
}

let lightboxContainer = null;
let currentGallery = [];
let currentIndex = 0;

function createLightboxContainer() {
  if (lightboxContainer) return;

  lightboxContainer = document.createElement('div');
  lightboxContainer.className = 'lightbox';
  lightboxContainer.innerHTML = `
    <div class="lightbox-backdrop" data-lightbox-close></div>
    <div class="lightbox-content">
      <button class="lightbox-close" data-lightbox-close aria-label="Close">
        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
          <line x1="18" y1="6" x2="6" y2="18"></line>
          <line x1="6" y1="6" x2="18" y2="18"></line>
        </svg>
      </button>
      <button class="lightbox-prev" data-lightbox-prev aria-label="Previous">
        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
          <polyline points="15 18 9 12 15 6"></polyline>
        </svg>
      </button>
      <button class="lightbox-next" data-lightbox-next aria-label="Next">
        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
          <polyline points="9 18 15 12 9 6"></polyline>
        </svg>
      </button>
      <div class="lightbox-image-container">
        <img class="lightbox-image" src="" alt="" data-lightbox-image>
        <div class="lightbox-loading">
          <div class="spinner"></div>
        </div>
      </div>
      <div class="lightbox-caption" data-lightbox-caption></div>
      <div class="lightbox-counter" data-lightbox-counter></div>
    </div>
  `;

  document.body.appendChild(lightboxContainer);

  // 
  lightboxContainer.querySelectorAll('[data-lightbox-close]').forEach(el => {
    el.addEventListener('click', closeLightbox);
  });

  lightboxContainer.querySelector('[data-lightbox-prev]').addEventListener('click', prevImage);
  lightboxContainer.querySelector('[data-lightbox-next]').addEventListener('click', nextImage);

  // 
  document.addEventListener('keydown', handleKeyboard);
}

function openLightbox(trigger) {
  const gallery = trigger.dataset.lightboxGallery;
  const src = trigger.dataset.lightbox || trigger.href;
  const caption = trigger.dataset.lightboxCaption || trigger.title || '';

  // 
  if (gallery) {
    currentGallery = Array.from(document.querySelectorAll(`[data-lightbox-gallery="${gallery}"]`)).map(el => ({
      src: el.dataset.lightbox || el.href,
      caption: el.dataset.lightboxCaption || el.title || '',
    }));
    currentIndex = currentGallery.findIndex(item => item.src === src);
  } else {
    currentGallery = [{ src, caption }];
    currentIndex = 0;
  }

  showImage(currentIndex);
  lightboxContainer.classList.add('open');
  document.body.classList.add('overflow-hidden');

  // 
  updateNavigation();
}

function closeLightbox() {
  lightboxContainer.classList.remove('open');
  document.body.classList.remove('overflow-hidden');
}

function showImage(index) {
  const image = lightboxContainer.querySelector('[data-lightbox-image]');
  const caption = lightboxContainer.querySelector('[data-lightbox-caption]');
  const counter = lightboxContainer.querySelector('[data-lightbox-counter]');
  const loading = lightboxContainer.querySelector('.lightbox-loading');

  const item = currentGallery[index];
  if (!item) return;

  // 
  loading.style.display = 'flex';
  image.style.opacity = '0';

  // 
  const img = new Image();
  img.onload = () => {
    image.src = item.src;
    image.alt = item.caption;
    image.style.opacity = '1';
    loading.style.display = 'none';
  };
  img.onerror = () => {
    loading.style.display = 'none';
  };
  img.src = item.src;

  // 
  caption.textContent = item.caption;
  caption.style.display = item.caption ? 'block' : 'none';

  // 
  if (currentGallery.length > 1) {
    counter.textContent = `${index + 1} / ${currentGallery.length}`;
    counter.style.display = 'block';
  } else {
    counter.style.display = 'none';
  }
}

function prevImage() {
  currentIndex = (currentIndex - 1 + currentGallery.length) % currentGallery.length;
  showImage(currentIndex);
  updateNavigation();
}

function nextImage() {
  currentIndex = (currentIndex + 1) % currentGallery.length;
  showImage(currentIndex);
  updateNavigation();
}

function updateNavigation() {
  const prevBtn = lightboxContainer.querySelector('[data-lightbox-prev]');
  const nextBtn = lightboxContainer.querySelector('[data-lightbox-next]');

  if (currentGallery.length <= 1) {
    prevBtn.style.display = 'none';
    nextBtn.style.display = 'none';
  } else {
    prevBtn.style.display = 'flex';
    nextBtn.style.display = 'flex';
  }
}

function handleKeyboard(e) {
  if (!lightboxContainer.classList.contains('open')) return;

  switch (e.key) {
    case 'Escape':
      closeLightbox();
      break;
    case 'ArrowLeft':
      prevImage();
      break;
    case 'ArrowRight':
      nextImage();
      break;
  }
}

export default { initLightbox, openLightbox, closeLightbox };
