/**
 * 
 * 
 */

export function initForms() {
  const forms = document.querySelectorAll('[data-form]');
  forms.forEach(form => new Form(form));
}

class Form {
  constructor(element) {
    this.element = element;
    this.inputs = element.querySelectorAll('input, textarea, select');
    this.submitBtn = element.querySelector('[type="submit"]');

    this.init();
  }

  init() {
    // 
    this.element.setAttribute('novalidate', '');

    // 
    this.inputs.forEach(input => {
      input.addEventListener('blur', () => this.validateInput(input));
      input.addEventListener('input', () => this.clearError(input));
    });

    // 
    this.element.addEventListener('submit', (e) => this.handleSubmit(e));
  }

  validateInput(input) {
    const rules = this.getValidationRules(input);
    const value = input.value.trim();
    let error = null;

    // 
    if (rules.required && !value) {
      error = input.dataset.errorRequired || '';
    }
    // 
    else if (rules.email && value && !this.isValidEmail(value)) {
      error = input.dataset.errorEmail || '';
    }
    // 
    else if (rules.phone && value && !this.isValidPhone(value)) {
      error = input.dataset.errorPhone || '';
    }
    // 
    else if (rules.minlength && value.length < rules.minlength) {
      error = input.dataset.errorMinlength || ` ${rules.minlength} `;
    }
    // 
    else if (rules.maxlength && value.length > rules.maxlength) {
      error = input.dataset.errorMaxlength || ` ${rules.maxlength} `;
    }
    // 
    else if (rules.confirm) {
      const targetInput = this.element.querySelector(`[name="${rules.confirm}"]`);
      if (targetInput && value !== targetInput.value) {
        error = input.dataset.errorConfirm || '';
      }
    }
    // 
    else if (rules.pattern && value && !new RegExp(rules.pattern).test(value)) {
      error = input.dataset.errorPattern || '';
    }

    if (error) {
      this.showError(input, error);
      return false;
    }

    this.showSuccess(input);
    return true;
  }

  getValidationRules(input) {
    return {
      required: input.hasAttribute('required'),
      email: input.type === 'email' || input.dataset.validateEmail !== undefined,
      phone: input.dataset.validatePhone !== undefined,
      minlength: parseInt(input.getAttribute('minlength')) || null,
      maxlength: parseInt(input.getAttribute('maxlength')) || null,
      pattern: input.getAttribute('pattern'),
      confirm: input.dataset.validateConfirm,
    };
  }

  showError(input, message) {
    const group = input.closest('.form-group') || input.parentElement;
    const existingError = group.querySelector('.form-error');

    input.classList.add('form-input-error');
    input.classList.remove('form-input-success');

    if (existingError) {
      existingError.textContent = message;
    } else {
      const errorEl = document.createElement('div');
      errorEl.className = 'form-error';
      errorEl.textContent = message;
      group.appendChild(errorEl);
    }
  }

  showSuccess(input) {
    const group = input.closest('.form-group') || input.parentElement;
    const existingError = group.querySelector('.form-error');

    input.classList.remove('form-input-error');
    input.classList.add('form-input-success');

    if (existingError) {
      existingError.remove();
    }
  }

  clearError(input) {
    const group = input.closest('.form-group') || input.parentElement;
    const existingError = group.querySelector('.form-error');

    input.classList.remove('form-input-error', 'form-input-success');

    if (existingError) {
      existingError.remove();
    }
  }

  validateAll() {
    let isValid = true;

    this.inputs.forEach(input => {
      if (!this.validateInput(input)) {
        isValid = false;
      }
    });

    return isValid;
  }

  async handleSubmit(e) {
    e.preventDefault();

    if (!this.validateAll()) {
      // 
      const firstError = this.element.querySelector('.form-input-error');
      firstError?.focus();
      return;
    }

    // 
    this.setLoading(true);

    try {
      const formData = new FormData(this.element);
      const action = this.element.action;
      const method = this.element.method || 'POST';

      //  FormData  URLSearchParams (application/x-www-form-urlencoded)
      const urlSearchParams = new URLSearchParams();
      for (const [key, value] of formData.entries()) {
        urlSearchParams.append(key, value);
      }

      const response = await fetch(action, {
        method,
        body: urlSearchParams,
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/x-www-form-urlencoded',
        },
      });

      const data = await response.json();

      if (response.ok) {
        this.onSuccess(data);
      } else {
        this.onError(data);
      }
    } catch (error) {
      this.onError({ message: '' });
    } finally {
      this.setLoading(false);
    }
  }

  setLoading(loading) {
    if (loading) {
      this.submitBtn?.classList.add('btn-loading');
      this.submitBtn?.setAttribute('disabled', '');
    } else {
      this.submitBtn?.classList.remove('btn-loading');
      this.submitBtn?.removeAttribute('disabled');
    }
  }

  onSuccess(data) {
    // 
    this.element.dispatchEvent(new CustomEvent('form:success', { detail: data }));

    // 
    const successMessage = this.element.dataset.successMessage || '';
    this.showMessage(successMessage, 'success');

    // 
    if (this.element.dataset.resetOnSuccess !== 'false') {
      this.element.reset();
      this.inputs.forEach(input => this.clearError(input));
    }

    // 
    if (data.redirect) {
      window.location.href = data.redirect;
    }
  }

  onError(data) {
    // 
    this.element.dispatchEvent(new CustomEvent('form:error', { detail: data }));

    // 
    const errorMessage = data.message || '';
    this.showMessage(errorMessage, 'error');

    // 
    if (data.errors) {
      Object.entries(data.errors).forEach(([field, message]) => {
        const input = this.element.querySelector(`[name="${field}"]`);
        if (input) {
          this.showError(input, message);
        }
      });
    }
  }

  showMessage(message, type) {
    // 
    let container = this.element.querySelector('.form-message');
    if (!container) {
      container = document.createElement('div');
      container.className = 'form-message';
      this.element.prepend(container);
    }

    container.textContent = message;
    container.className = `form-message form-message-${type}`;

    // 
    setTimeout(() => {
      container.remove();
    }, 5000);
  }

  // 
  isValidEmail(email) {
    return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email);
  }

  isValidPhone(phone) {
    return /^1[3-9]\d{9}$/.test(phone);
  }
}

export default { initForms, Form };
