/**
 * 
 *  Intersection Observer API
 */

export function lazyLoadImages() {
  const lazyImages = document.querySelectorAll('[data-lazy-src]');

  if ('IntersectionObserver' in window) {
    const imageObserver = new IntersectionObserver((entries, observer) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          const img = entry.target;
          loadImage(img);
          observer.unobserve(img);
        }
      });
    }, {
      rootMargin: '50px 0px',
      threshold: 0.01,
    });

    lazyImages.forEach(img => {
      imageObserver.observe(img);
    });
  } else {
    // 
    lazyImages.forEach(img => loadImage(img));
  }
}

function loadImage(img) {
  const src = img.dataset.lazySrc;
  const srcset = img.dataset.lazySrcset;

  if (src) {
    img.src = src;
  }

  if (srcset) {
    img.srcset = srcset;
  }

  img.classList.add('lazy-loaded');
  img.removeAttribute('data-lazy-src');
  img.removeAttribute('data-lazy-srcset');
}

/**
 * 
 */
export function lazyLoadBackgrounds() {
  const lazyBgs = document.querySelectorAll('[data-lazy-bg]');

  if ('IntersectionObserver' in window) {
    const bgObserver = new IntersectionObserver((entries, observer) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          const el = entry.target;
          el.style.backgroundImage = `url(${el.dataset.lazyBg})`;
          el.classList.add('lazy-loaded');
          el.removeAttribute('data-lazy-bg');
          observer.unobserve(el);
        }
      });
    }, {
      rootMargin: '50px 0px',
      threshold: 0.01,
    });

    lazyBgs.forEach(el => {
      bgObserver.observe(el);
    });
  } else {
    lazyBgs.forEach(el => {
      el.style.backgroundImage = `url(${el.dataset.lazyBg})`;
      el.classList.add('lazy-loaded');
    });
  }
}

export default { lazyLoadImages, lazyLoadBackgrounds };
