/**
 * 
 * 
 */

export function initCarousel() {
  const carousels = document.querySelectorAll('[data-carousel]');
  carousels.forEach(carousel => new Carousel(carousel));
}

class Carousel {
  constructor(element) {
    this.element = element;
    this.track = element.querySelector('[data-carousel-track]');
    this.slides = element.querySelectorAll('[data-carousel-slide]');
    this.prevBtn = element.querySelector('[data-carousel-prev]');
    this.nextBtn = element.querySelector('[data-carousel-next]');
    this.indicators = element.querySelector('[data-carousel-indicators]');

    if (!this.track || this.slides.length === 0) return;

    this.currentIndex = 0;
    this.slideCount = this.slides.length;
    this.autoplay = element.dataset.carouselAutoplay !== 'false';
    this.interval = parseInt(element.dataset.carouselInterval) || 5000;
    this.autoplayTimer = null;

    this.init();
  }

  init() {
    // 
    this.createIndicators();

    // 
    this.prevBtn?.addEventListener('click', () => this.prev());
    this.nextBtn?.addEventListener('click', () => this.next());

    // 
    this.element.addEventListener('keydown', (e) => {
      if (e.key === 'ArrowLeft') this.prev();
      if (e.key === 'ArrowRight') this.next();
    });

    // 
    this.initTouchEvents();

    // 
    if (this.autoplay) {
      this.startAutoplay();

      // 
      this.element.addEventListener('mouseenter', () => this.stopAutoplay());
      this.element.addEventListener('mouseleave', () => this.startAutoplay());
    }

    // 
    this.goTo(0);
  }

  createIndicators() {
    if (!this.indicators) return;

    this.indicators.innerHTML = '';
    for (let i = 0; i < this.slideCount; i++) {
      const dot = document.createElement('button');
      dot.className = 'carousel-indicator';
      dot.setAttribute('aria-label', `Go to slide ${i + 1}`);
      dot.addEventListener('click', () => this.goTo(i));
      this.indicators.appendChild(dot);
    }
  }

  updateIndicators() {
    if (!this.indicators) return;

    const dots = this.indicators.querySelectorAll('.carousel-indicator');
    dots.forEach((dot, index) => {
      dot.classList.toggle('active', index === this.currentIndex);
    });
  }

  initTouchEvents() {
    let startX = 0;
    let isDragging = false;

    this.track.addEventListener('touchstart', (e) => {
      startX = e.touches[0].clientX;
      isDragging = true;
      this.stopAutoplay();
    }, { passive: true });

    this.track.addEventListener('touchmove', (e) => {
      if (!isDragging) return;
      // 
    }, { passive: true });

    this.track.addEventListener('touchend', (e) => {
      if (!isDragging) return;
      isDragging = false;

      const endX = e.changedTouches[0].clientX;
      const diff = startX - endX;

      if (Math.abs(diff) > 50) {
        if (diff > 0) {
          this.next();
        } else {
          this.prev();
        }
      }

      if (this.autoplay) {
        this.startAutoplay();
      }
    });
  }

  goTo(index) {
    if (index < 0) {
      index = this.slideCount - 1;
    } else if (index >= this.slideCount) {
      index = 0;
    }

    this.currentIndex = index;
    this.track.style.transform = `translateX(-${index * 100}%)`;

    //  slide 
    this.slides.forEach((slide, i) => {
      slide.classList.toggle('active', i === index);
      slide.setAttribute('aria-hidden', i !== index);
    });

    this.updateIndicators();
  }

  next() {
    this.goTo(this.currentIndex + 1);
  }

  prev() {
    this.goTo(this.currentIndex - 1);
  }

  startAutoplay() {
    if (this.autoplayTimer) return;
    this.autoplayTimer = setInterval(() => this.next(), this.interval);
  }

  stopAutoplay() {
    if (this.autoplayTimer) {
      clearInterval(this.autoplayTimer);
      this.autoplayTimer = null;
    }
  }

  destroy() {
    this.stopAutoplay();
  }
}

export default { initCarousel, Carousel };
