/**
 * Tab 
 * URL 
 */

export function initTabs() {
  const tabContainers = document.querySelectorAll('[data-tabs]');
  tabContainers.forEach(container => new Tabs(container));
}

class Tabs {
  constructor(element) {
    this.element = element;
    this.tabList = element.querySelector('[data-tab-list]');
    this.tabs = element.querySelectorAll('[data-tab]');
    this.panels = element.querySelectorAll('[data-tab-panel]');

    if (!this.tabList || this.tabs.length === 0) return;

    this.init();
  }

  init() {
    //  ARIA 
    this.tabList.setAttribute('role', 'tablist');

    this.tabs.forEach((tab, index) => {
      tab.setAttribute('role', 'tab');
      tab.setAttribute('aria-selected', 'false');
      tab.setAttribute('tabindex', '-1');
      tab.id = tab.id || `tab-${index}`;

      // 
      const panelId = tab.dataset.tab;
      const panel = this.element.querySelector(`[data-tab-panel="${panelId}"]`);
      if (panel) {
        tab.setAttribute('aria-controls', panel.id || `panel-${index}`);
        panel.setAttribute('role', 'tabpanel');
        panel.setAttribute('aria-labelledby', tab.id);
        panel.id = panel.id || `panel-${index}`;
      }
    });

    // 
    this.tabs.forEach(tab => {
      tab.addEventListener('click', (e) => {
        e.preventDefault();
        this.activate(tab);
      });

      tab.addEventListener('keydown', (e) => this.handleKeydown(e, tab));
    });

    //  URL 
    const hash = window.location.hash.slice(1);
    const targetTab = this.element.querySelector(`[data-tab="${hash}"]`);

    //  tab
    if (targetTab) {
      this.activate(targetTab);
    } else {
      this.activate(this.tabs[0]);
    }
  }

  activate(tab) {
    const panelId = tab.dataset.tab;
    const panel = this.element.querySelector(`[data-tab-panel="${panelId}"]`);

    //  tab 
    this.tabs.forEach(t => {
      t.classList.remove('active');
      t.setAttribute('aria-selected', 'false');
      t.setAttribute('tabindex', '-1');
    });

    // 
    this.panels.forEach(p => {
      p.classList.remove('active');
      p.setAttribute('hidden', '');
    });

    //  tab
    tab.classList.add('active');
    tab.setAttribute('aria-selected', 'true');
    tab.setAttribute('tabindex', '0');

    // 
    if (panel) {
      panel.classList.add('active');
      panel.removeAttribute('hidden');
    }

    //  URL
    if (this.element.dataset.tabsUpdateUrl !== 'false') {
      history.replaceState(null, '', `#${panelId}`);
    }

    // 
    this.element.dispatchEvent(new CustomEvent('tab:change', {
      detail: { tab, panel, panelId },
    }));
  }

  handleKeydown(e, tab) {
    const tabsArray = Array.from(this.tabs);
    const currentIndex = tabsArray.indexOf(tab);
    let newIndex;

    switch (e.key) {
      case 'ArrowLeft':
      case 'ArrowUp':
        e.preventDefault();
        newIndex = currentIndex - 1;
        if (newIndex < 0) newIndex = tabsArray.length - 1;
        tabsArray[newIndex].focus();
        this.activate(tabsArray[newIndex]);
        break;

      case 'ArrowRight':
      case 'ArrowDown':
        e.preventDefault();
        newIndex = currentIndex + 1;
        if (newIndex >= tabsArray.length) newIndex = 0;
        tabsArray[newIndex].focus();
        this.activate(tabsArray[newIndex]);
        break;

      case 'Home':
        e.preventDefault();
        tabsArray[0].focus();
        this.activate(tabsArray[0]);
        break;

      case 'End':
        e.preventDefault();
        tabsArray[tabsArray.length - 1].focus();
        this.activate(tabsArray[tabsArray.length - 1]);
        break;
    }
  }
}

export default { initTabs, Tabs };
